*{box-sizing: border-box;}
*:before,*:after{box-sizing: border-box;}
*{margin: 0;padding: 0;}
ul,ol{list-style: none;}
a{color:inherit;text-decoration: none;}
body{
    background:("8.jpg") ;
  background-position: center center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;   
}
.globalHeader{
margin: 20px;
}
@media (min-width:500px){
    .globalHeader{
margin: 80px 0 100px;
}
}
.searchForm{
    display: flex;
justify-content: space-between;
max-width: 400px;
}
@media (min-width:500px){
    .searchForm{
max-width: 400px;
margin-left: auto;
margin-right: auto;
}
}
.searchForm > input{
    border: 1px solid #ddd;
    width:100%;
    margin-right: 10px;
    height: 40px;
    padding:0 10px ;
    border-radius: 4px;
}
.searchForm > button{
    white-space: nowrap;
    padding: 0 28px;
    border-radius: 4px;
    background: #4890FD;
    border: none;
    color: white;
    font-size: 16px;
}
.globalMain{
    max-width: 900px;
    margin-right: auto;
    margin-left: auto;
}
.siteList{
    display: flex;
    flex-wrap: wrap;
    margin:20px ;
    justify-content: space-between;
}
@media (min-width:500px){
.siteList{
    margin-right: -25px;
    margin-left: 0;
    justify-content: flex-start;
}
}
.siteList>li{   
 margin-bottom: 10px;
}
@media(min-width:500px){
    .siteList>li{   
 margin-right: 25px;
}
}
.siteList .site{
    width: 160px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    background: white;
    border: 1px solid #ddd;
   border-radius: 4px;
   padding: 20px 0;
   position: relative;
   cursor: pointer;
}
.siteList .site>.logo{
    width: 64px;
    height: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 64px;
    text-transform:uppercase ;
}    
.siteList .site>.link{
font-size: 14px;
margin-top: 4px;
}
.siteList .site>.close{
position: absolute;
right: 5px;
top: 5px;
display: none;
cursor: default;
}
.siteList .site:hover>.close{
    display: block;
}
.siteList .addButton{
    width: 160px;
    border: 1px solid #ddd;
    background: white;
    display: flex;
justify-content: center;
align-items: center;
flex-direction: column;
padding: 20px 0;
}
.siteList .addButton .icon{
    width: 56px;
    height: 56px;
}
.siteList .addButton .text{
    font-size: 14px;
margin-top: 4px;
}
.siteList .addButton .icon-wrapper{
width: 64px;
height: 64px;
display: flex;
justify-content: center;
align-items: center;
}
